	module.exports = {"pathContext":{"docHtml":"<h1>Architectural Overview</h1>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/argocd_architecture-643caa8250f7d6d582497dca23c685f2-d0cc3.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 95.28936742934052%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Argo CD Architecture\"\n        title=\"\"\n        src=\"/static/argocd_architecture-643caa8250f7d6d582497dca23c685f2-fb8a0.png\"\n        srcset=\"/static/argocd_architecture-643caa8250f7d6d582497dca23c685f2-1a291.png 148w,\n/static/argocd_architecture-643caa8250f7d6d582497dca23c685f2-2bc4a.png 295w,\n/static/argocd_architecture-643caa8250f7d6d582497dca23c685f2-fb8a0.png 590w,\n/static/argocd_architecture-643caa8250f7d6d582497dca23c685f2-d0cc3.png 743w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>Components</h2>\n<h3>API Server</h3>\n<p>The API server is a gRPC/REST server which exposes the API consumed by the Web UI, CLI, and CI/CD\nsystems. It has the following responsibilities:</p>\n<ul>\n<li>application management and status reporting</li>\n<li>invoking of application operations (e.g. sync, rollback, user-defined actions)</li>\n<li>repository and cluster credential management (stored as K8s secrets)</li>\n<li>authentication and auth delegation to external identity providers</li>\n<li>RBAC enforcement</li>\n<li>listener/forwarder for Git webhook events</li>\n</ul>\n<h3>Repository Server</h3>\n<p>The repository server is an internal service which maintains a local cache of the Git repository\nholding the application manifests. It is responsible for generating and returning the Kubernetes\nmanifests when provided the following inputs:</p>\n<ul>\n<li>repository URL</li>\n<li>revision (commit, tag, branch)</li>\n<li>application path</li>\n<li>template specific settings: parameters, ksonnet environments, helm values.yaml</li>\n</ul>\n<h3>Application Controller</h3>\n<p>The application controller is a Kubernetes controller which continuously monitors running\napplications and compares the current, live state against the desired target state (as specified in\nthe repo). It detects <code>OutOfSync</code> application state and optionally takes corrective action. It\nis responsible for invoking any user-defined hooks for lifcecycle events (PreSync, Sync, PostSync)</p>","docPath":"argo-cd/docs/operator-manual/architecture","proj":"argo-cd"}}

